	module.exports = {"data":{"markdownRemark":{"html":"<p>The Hacker Dashboard enables you to view and manage all of your invitations. There are 4 tabs that you can view:</p>\n<ul>\n<li><a href=\"#overview\">Overview</a></li>\n<li><a href=\"#programs\">My Programs</a></li>\n<li><a href=\"#pending\">Pending Invitations</a></li>\n<li><a href=\"#bookmarked\">Bookmarked Programs</a></li>\n</ul>\n<h3 id=\"overview\">Overview</h3>\n<p>The Overview page is your guide to help you get started on HackerOne. You'll be directed to the right pages to help you get the information you need to successfully start out on HackerOne.</p>\n<p>We also provide you with a getting started checklist with 4 tasks to complete. Completion of the tasks will help guide you to be more successful on the platform.</p>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/hackerdashboard_checklist-7b69ede3c28fea4f77e15f8a7ff61f99-92eaa.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 500px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 65.82537517053207%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"hacker dashboard overview page with checklist\"\n        title=\"\"\n        src=\"/static/hackerdashboard_checklist-7b69ede3c28fea4f77e15f8a7ff61f99-e7ffa.png\"\n        srcset=\"/static/hackerdashboard_checklist-7b69ede3c28fea4f77e15f8a7ff61f99-49813.png 125w,\n/static/hackerdashboard_checklist-7b69ede3c28fea4f77e15f8a7ff61f99-dc8a5.png 250w,\n/static/hackerdashboard_checklist-7b69ede3c28fea4f77e15f8a7ff61f99-e7ffa.png 500w,\n/static/hackerdashboard_checklist-7b69ede3c28fea4f77e15f8a7ff61f99-dda24.png 750w,\n/static/hackerdashboard_checklist-7b69ede3c28fea4f77e15f8a7ff61f99-c5f18.png 1000w,\n/static/hackerdashboard_checklist-7b69ede3c28fea4f77e15f8a7ff61f99-92eaa.png 1466w\"\n        sizes=\"(max-width: 500px) 100vw, 500px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<p>After you've submitted your first vulnerability, you'll be able to keep track of your statistics regarding the number of valid reports you have and the bounties you've earned.  </p>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/hackerdashboard_stats-10b6ba1bffd5667f421dd481a5bf0b50-92eaa.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 500px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 57.912687585266035%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"hacker dashboard statistics\"\n        title=\"\"\n        src=\"/static/hackerdashboard_stats-10b6ba1bffd5667f421dd481a5bf0b50-e7ffa.png\"\n        srcset=\"/static/hackerdashboard_stats-10b6ba1bffd5667f421dd481a5bf0b50-49813.png 125w,\n/static/hackerdashboard_stats-10b6ba1bffd5667f421dd481a5bf0b50-dc8a5.png 250w,\n/static/hackerdashboard_stats-10b6ba1bffd5667f421dd481a5bf0b50-e7ffa.png 500w,\n/static/hackerdashboard_stats-10b6ba1bffd5667f421dd481a5bf0b50-dda24.png 750w,\n/static/hackerdashboard_stats-10b6ba1bffd5667f421dd481a5bf0b50-c5f18.png 1000w,\n/static/hackerdashboard_stats-10b6ba1bffd5667f421dd481a5bf0b50-92eaa.png 1466w\"\n        sizes=\"(max-width: 500px) 100vw, 500px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<h3 id=\"programs\">My Programs</h3>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/hacker-dashboard-my-programs-d380e376f0d8ae056fe048c47de81eba-5ccb9.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 500px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 55.71340333539222%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"hacker dashboard my programs\"\n        title=\"\"\n        src=\"/static/hacker-dashboard-my-programs-d380e376f0d8ae056fe048c47de81eba-e7ffa.png\"\n        srcset=\"/static/hacker-dashboard-my-programs-d380e376f0d8ae056fe048c47de81eba-49813.png 125w,\n/static/hacker-dashboard-my-programs-d380e376f0d8ae056fe048c47de81eba-dc8a5.png 250w,\n/static/hacker-dashboard-my-programs-d380e376f0d8ae056fe048c47de81eba-e7ffa.png 500w,\n/static/hacker-dashboard-my-programs-d380e376f0d8ae056fe048c47de81eba-dda24.png 750w,\n/static/hacker-dashboard-my-programs-d380e376f0d8ae056fe048c47de81eba-c5f18.png 1000w,\n/static/hacker-dashboard-my-programs-d380e376f0d8ae056fe048c47de81eba-22484.png 1500w,\n/static/hacker-dashboard-my-programs-d380e376f0d8ae056fe048c47de81eba-5ccb9.png 1619w\"\n        sizes=\"(max-width: 500px) 100vw, 500px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<p>The My Programs page enables you to better manage all of the programs you're a part of. You can view the private programs you've accepted to participate in and the public programs you've hacked on.</p>\n<p>For each program, you can view:</p>\n<ul>\n<li>The total number of reports resolved</li>\n<li>The minimum bounty</li>\n<li>The average bounty</li>\n<li>Your stats: The total number of reports you've submitted, the number of  reports that were valid, and the total amount of bounties you've earned</li>\n</ul>\n<p>You can elect to leave the private programs that you no longer have interest to participate in by clicking <strong>Leave Program</strong> next to the program you want to leave.</p>\n<h3 id=\"pending\">Pending Invitations</h3>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/hacker-dashboard-2-318f7b030bb257768909042a61718b5e-53be2.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 500px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 44.54545454545455%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"hacker dashboard pending invitations\"\n        title=\"\"\n        src=\"/static/hacker-dashboard-2-318f7b030bb257768909042a61718b5e-e7ffa.png\"\n        srcset=\"/static/hacker-dashboard-2-318f7b030bb257768909042a61718b5e-49813.png 125w,\n/static/hacker-dashboard-2-318f7b030bb257768909042a61718b5e-dc8a5.png 250w,\n/static/hacker-dashboard-2-318f7b030bb257768909042a61718b5e-e7ffa.png 500w,\n/static/hacker-dashboard-2-318f7b030bb257768909042a61718b5e-dda24.png 750w,\n/static/hacker-dashboard-2-318f7b030bb257768909042a61718b5e-c5f18.png 1000w,\n/static/hacker-dashboard-2-318f7b030bb257768909042a61718b5e-22484.png 1500w,\n/static/hacker-dashboard-2-318f7b030bb257768909042a61718b5e-53be2.png 2860w\"\n        sizes=\"(max-width: 500px) 100vw, 500px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<p>The Pending Invitations tab enables you to view all of the invitations that you haven’t accepted.</p>\n<p>For each program you have an invite to, you can view these options:</p>\n<table>\n<thead>\n<tr>\n<th>Option</th>\n<th>Detail</th>\n</tr>\n</thead>\n<tbody>\n<tr>\n<td>Invitation Expiration Time</td>\n<td>Invitations don't last forever! They have an expiration time where you can no longer take action to accept them.</td>\n</tr>\n<tr>\n<td>Launch Date</td>\n<td>The date the program started to accept vulnerabilities.</td>\n</tr>\n<tr>\n<td>Bugs resolved</td>\n<td>The total number of vulnerabilities the program has fixed.</td>\n</tr>\n<tr>\n<td><a href=\"/programs/response-target-indicators.html\">Response Efficiency</a></td>\n<td>The percentage of reports that are responded to on time within the last 90 days.</td>\n</tr>\n<tr>\n<td>Minimum Bounty</td>\n<td>The minimum bounty that will be given for a valid vulnerability. If the field is marked with a \n<code>-</code>\n this means that there is no minimum bounty, or the program chose not to display this information on their metric display settings.</td>\n</tr>\n<tr>\n<td>Average Bounty</td>\n<td>The average bounty that will be given for a valid vulnerability. If the field is marked with a \n<code>-</code>\n this means that there is no average bounty, or the program chose not to display this information on their metric display settings.</td>\n</tr>\n</tbody>\n</table>\n<p>Click <b>View Invitation</b> to review the invitation and take action to accept or reject it.</p>\n<h3 id=\"bookmarked\">Bookmarked Programs</h3>\n<p>The Bookmarked Programs tab enables you to view the list of programs that you've marked as your favorites in the directory. This enables you to better keep track of the programs you're most interested in.</p>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/bookmarked-34218eca20b716077de52812f86c94b0-c1cde.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 500px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 22.353760445682454%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"bookmarked programs\"\n        title=\"\"\n        src=\"/static/bookmarked-34218eca20b716077de52812f86c94b0-e7ffa.png\"\n        srcset=\"/static/bookmarked-34218eca20b716077de52812f86c94b0-49813.png 125w,\n/static/bookmarked-34218eca20b716077de52812f86c94b0-dc8a5.png 250w,\n/static/bookmarked-34218eca20b716077de52812f86c94b0-e7ffa.png 500w,\n/static/bookmarked-34218eca20b716077de52812f86c94b0-dda24.png 750w,\n/static/bookmarked-34218eca20b716077de52812f86c94b0-c5f18.png 1000w,\n/static/bookmarked-34218eca20b716077de52812f86c94b0-22484.png 1500w,\n/static/bookmarked-34218eca20b716077de52812f86c94b0-c1cde.png 2872w\"\n        sizes=\"(max-width: 500px) 100vw, 500px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<p>To remove programs from your bookmarked list, click the blue star icon.</p>","frontmatter":{"title":"Hacker Dashboard","id":"hackers/hacker-dashboard","path":"/hackers/hacker-dashboard.html","description":null}}},"pathContext":{}}

